<template>
  <table class="table table-sm">
    <thead>
      <tr>
        <th v-for="(col, index) in cols" :key="index" @click="changeSort(col.key)">
          <span>{{ col.name }}</span>
          <template v-if="col.sortable">
            <span
              v-if="sortBy[0] === col.key"
              :class="`fas fa-sort-${sortBy[1] ? 'up' : 'down'}`"
            ></span>
            <span v-else class="fas fa-sort inactive"></span>
          </template>
        </th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="(row, index) in rows" :key="index">
        <td v-for="(col, index) in cols" :key="index">
          <template v-if="typeof row.value[col.key] === 'object'">
            <template v-if="row.value[col.key].type === 'checkbox'">
              <input
                type="checkbox"
                v-model="row.value[col.key].checked"
                @change="row.value[col.key].onChange.call(this, arguments[0])"
              />
            </template>
            <template v-if="row.value[col.key].type === 'input'">
              <input
                type="text"
                :value="row.value[col.key].value"
                @change="row.value[col.key].onChange.call(this, arguments[0])"
              />
            </template>
            <template v-if="row.value[col.key].type === 'select'">
              <select @change="row.value[col.key].onChange.call(this, arguments[0])">
                <option
                  v-for="opt in row.value[col.key].opts"
                  :key="opt.value"
                  :selected="row.value[col.key].value === opt.value"
                  :value="opt.value"
                  >{{ opt.text }}</option
                >
              </select>
            </template>
            <template v-if="row.value[col.key].type === 'button'">
              <a
                href="#"
                v-html="row.value[col.key].content"
                @click.prevent="row.value[col.key].onClick.call(this, arguments[0])"
              ></a>
            </template>
          </template>
          <template v-else>
            <span v-html="row.value[col.key]"></span>
          </template>
        </td>
      </tr>
    </tbody>
  </table>
</template>

<script>
import Vue from "vue";

function fromPairs(pairs) {
  const result = {};
  for (const [k, v] of pairs) {
    result[k] = v;
  }
  return result;
}

export default {
  name: "editor-page-table",
  props: ["tableConfig"],
  data() {
    return {
      rows: [],
      cols: [],
      sortBy: [null, null],
    };
  },
  methods: {
    updateTableData() {
      const conf = this.tableConfig;
      const sortableCols = conf.sortableColumns || [];
      if (conf.refreshOnDataLoaded) {
        // event.on(event.BEFORE_DRAW, () => {
        //     this.updateData();
        // });
      }
      const cols = conf.columns.map(x =>
        typeof x === "string"
          ? { name: x, key: x, sortable: sortableCols.includes(x) }
          : x,
      );
      const rows = Array.from(Array(conf.numberOfRows())).map((d, i) => ({
        value: fromPairs(cols.map(col => [col.key, conf.dataFor(i, col.key)])),
      }));
      this.rows = rows;
      this.cols = cols;
    },
    updateData() {
      if (this.tableConfig.__tableUpdate) {
        this.updateTableData();
        Vue.delete(this.tableConfig, "__tableUpdate");
      } else {
        this.updateTableData();
        Vue.set(this.tableConfig, "__tableUpdate", true);
      }
    },
    update() {
      this.updateData();
    },
    rowClicked(item, index) {
      if (this.tableConfig.onClick) {
        this.tableConfig.onClick.call(this, item, index, this);
      }
    },
    changeSort(key) {
      let ascend = true;
      if (this.sortBy && this.sortBy[0] === key) {
        ascend = !this.sortBy[1];
      }
      this.sortBy = [key, ascend];

      this.rows = this.rows.sort((a, b) => {
        const va = a.value[key];
        const vb = b.value[key];
        return va === vb ? 0 : ascend === va > vb ? 1 : -1;
      });
    },
  },
  mounted() {
    if (this.tableConfig.ref) this.$root.$data.editorRef[this.tableConfig.ref] = this;
    this.updateTableData();
  },
};
</script>

<style lang="sass" scoped>
table
    margin-bottom: 0
    th
        cursor: pointer
    .inactive
        opacity: 0.2
.clickable
    cursor: pointer
input[type=text]
    width: 40px
    font-size: 11px
</style>
