<template>
  <b-list-group v-if="config.type === 'list'">
    <editor-item
      v-for="(item, index) in config.items"
      :key="index"
      :config="item"
      :scope="`${scope}-${index}`"
    />
  </b-list-group>
  <div class="custom-view" v-else-if="config.type === 'custom-legacy'">
    <div class="custom-view-header" v-html="config.header_html"></div>
    <div class="custom-view-body" v-html="config.body_html"></div>
  </div>
  <div v-else-if="config.type === 'table'" :style="tableStyle(config)">
    <editor-page-table :tableConfig="config" />
  </div>
  <component
    v-else-if="config.type === 'vue'"
    :is="config.component"
    :data="config.data"
    ref="cuscom"
  />
</template>

<script>
import EditorItem from "./EditorItem.vue";
import EditorPageTable from "./EditorPageTable.vue";

export default {
  name: "editor-view",
  components: { EditorItem, EditorPageTable },
  props: ["config", "scope"],
  methods: {
    tableStyle(config) {
      return `${
        config.height ? `height:${config.height}px;` : ""
      }overflow:auto;white-space:nowrap;`;
    },
  },
  mounted() {
    if ("ref" in this.config && this.config.ref && this.config.type === "vue") {
      this.$root.$data.editorRef[this.config.ref] = this.$refs.cuscom;
    }
  },
};
</script>

<style scoped lang="sass">
.custom-view
    padding: 5px
    .custom-view-body
        broder-top: 1px solid rgba(0,0,0,.05)
        margin-top: 1rem
        max-height: 300px
        overflow: auto
</style>
