<template>
  <b-list-group-item>
    <div v-if="config.type === 'text'">
      <label>{{ config.title }}</label>
      <div v-html="sanitizeHTML(config.content)" class="small text-muted"></div>
    </div>
    <div v-else-if="config.type === 'checkbox'" class="form-check">
      <b-form-checkbox v-model="value" :disabled="config.disabled">{{
        config.title
      }}</b-form-checkbox>
    </div>
    <div v-else-if="config.type === 'button'">
      <b-btn
        block
        size="sm"
        :variant="config.variant || 'secondary'"
        :id="`input-${scope}`"
        @click="config.action"
        >{{ config.title }}</b-btn
      >
    </div>
    <div v-else-if="config.type === 'input'">
      <template v-if="config.layout === 'compact'">
        <b-input-group size="sm" :prepend="config.title" :id="`input-${scope}`">
          <b-form-input
            v-model.trim="value"
            @blur="onBlur"
            @change="onClickApply"
            :disabled="config.disabled"
          ></b-form-input>
          <b-input-group-append>
            <b-btn variant="primary" @click="onClickApply">Apply</b-btn>
          </b-input-group-append>
        </b-input-group>
      </template>
      <template v-else>
        <label v-if="config.title" :for="`input-${scope}`">{{ config.title }}</label>
        <b-input-group size="sm" :id="`input-${scope}`">
          <b-form-input
            size="sm"
            type="text"
            v-model.trim="value"
            @blur="onBlur"
            @change="onClickApply"
            :disabled="config.disabled"
          />
          <b-input-group-append>
            <b-btn variant="primary" @click="onClickApply">Apply</b-btn>
          </b-input-group-append>
        </b-input-group>
      </template>
    </div>
    <div v-else-if="config.type === 'select'">
      <template v-if="config.layout === 'compact'">
        <b-input-group size="sm" :prepend="config.title" :id="`input-${scope}`">
          <b-form-select
            :options="config.options"
            v-model="value"
            :disabled="config.disabled"
          />
        </b-input-group>
      </template>
      <template v-else>
        <label v-if="config.title" :for="`input-${scope}`">{{ config.title }}</label>
        <b-form-select
          size="sm"
          :options="config.options"
          :id="`input-${scope}`"
          v-model="value"
          :disabled="config.disabled"
        />
      </template>
    </div>
    <component
      v-else-if="config.type === 'vue'"
      :is="config.component"
      ref="component"
      :data="typeof config.data === 'function' ? config.data() : config.data"
    />
  </b-list-group-item>
</template>

<script>
import sanitizeHTML from "sanitize-html";

function findPath(obj, path) {
  const s = path.split(".").map(x => (x[0] === "#" ? parseInt(x.slice(1)) : x));
  const p = s.slice(0, s.length - 1);
  const last = s[s.length - 1];
  let o = obj;
  for (const idx of p) {
    o = obj[idx];
  }
  return [o, last];
}

function get(obj, path) {
  const [o, last] = findPath(obj, path);
  return o[last];
}

function set(obj, path, value) {
  const [o, last] = findPath(obj, path);
  o[last] = value;
}

export default {
  name: "editor-item",
  props: ["config", "scope"],
  data() {
    return {
      value: null,
      commited: false,

      sanitizeHTML,
      justUpdated: false,
    };
  },
  methods: {
    onBlur() {
      this.commited = false;
      setTimeout(() => {
        if (!this.commited) this.update();
      }, 500);
    },
    onClickApply() {
      this.commited = true;
      this.updateBoundValue(this.value, 0);
    },
    updateBoundValue(val, oldVal) {
      if (this.justUpdated) {
        this.justUpdated = false;
        return;
      }
      if (oldVal === null) {
        return;
      }

      let hasFormat = false;
      if (this.config.type === "input") {
        if (this.config.format === "int") {
          val = parseInt(val);
          hasFormat = true;
        } else if (this.config.format === "float") {
          val = parseFloat(val);
          hasFormat = true;
        }
      }
      if (hasFormat && isNaN(val)) {
        alert("The input is invalid.");
        this.$nextTick(() => this.update());
        return;
      }
      if ("bind" in this.config) {
        const bind = this.config.bind;
        if (typeof bind === "object") {
          set(bind.object, bind.path, this.value);
          if (bind.callback) {
            bind.callback.call(this, val, oldVal);
          }
        }
      }
      if ("value" in this.config) {
        const value = this.config.value;
        if (typeof value === "object") {
          if (value.callback) {
            value.callback.call(this, val, oldVal);
          }
        }
      }
    },
    update(manually = true) {
      if (this.config.type === "vue") {
        this.$forceUpdate();
        return;
      }
      if ("bind" in this.config) {
        const bind = this.config.bind;
        if (typeof bind === "object") {
          this.justUpdated = true;
          this.value = get(bind.object, bind.path);
        }
      }
      if ("value" in this.config) {
        const value = this.config.value;
        if (typeof value === "object") {
          this.justUpdated = true;
          this.value = value.current;
        }
      }
      if (manually) this.$forceUpdate();
    },
  },
  watch: {
    value(val, oldVal) {
      if (this.config.type === "input") {
        return;
      }
      this.updateBoundValue(val, oldVal);
    },
  },
  created() {
    this.update(false);
  },
  mounted() {
    if (this.config.ref) {
      this.$root.$data.editorRef[this.config.ref] = this;
    }
  },
};
</script>

<style scoped lang="sass">
.form-check
    padding: 0
</style>
