<template>
  <div>
    <button class="oviz-editor-toggle-btn" @click="toggleEditor">Editor</button>
    <div class="oviz-editor-wrapper" :style="wrapperStyle">
      <Editor :config="config" :editorWidth="width" />
    </div>
  </div>
</template>

<script>
import Editor from "./components/StandaloneEditor.vue";

export default {
  components: {
    Editor,
  },
  props: {
    top: { default: 20 },
    margin: { default: 20 },
    position: { default: "right" },
    width: { default: 280 },
    shown: { default: false },
    initialConfig: { default: () => ({ sections: [] }) },
  },
  data() {
    return {
      config: null,
      show: false,
    };
  },
  methods: {
    toggleEditor() {
      this.show = !this.show;
    },
  },
  computed: {
    wrapperStyle() {
      const pos = this.position === "right" ? "right" : "left";
      const right = this.show
        ? `${pos}:${this.margin}px;`
        : `${pos}:-${this.width + 5}px;`;
      return `${right}top:${this.top}px;`;
    },
  },
  created() {
    this.config = this.initialConfig;
    this.show = this.shown;
    this.$root.$on("updateConfig", config => {
      this.config = config;
    });
  },
};
</script>

<style lang="scss">
.oviz-editor-wrapper {
  position: fixed;
  transition: all 0.3s;
}
</style>
