<template>
  <div class="v-editor" :style="editorStyle" role="tablist">
    <b-card
      no-body
      v-for="secDef in config.sections"
      :key="secDef.id"
      :id="`panel-${secDef.id}`"
      :style="{ background: cardBackground }"
      class="mb-1"
    >
      <b-card-header header-tag="header" class="p-1" role="tab">
        <b-btn
          block
          href="#"
          v-b-toggle="`accordion-${secDef.id}`"
          variant="secondary"
          class="sec-btn"
        >
          <span class="float-right"><i class="collapse-icon"></i></span>
          <i v-if="secDef.icon" :class="`fa fa-${secDef.icon}`"></i>
          <i v-else class="fa fa-fw fa-cog"></i>{{ secDef.title }}
        </b-btn>
      </b-card-header>
      <b-collapse
        :id="`accordion-${secDef.id}`"
        accordion="editor-accordion"
        role="tabpanel"
      >
        <template v-if="secDef.builtin === 'unknown'">
          <div class="p-y-2">Unknown</div>
        </template>
        <!-- Builtin: Files -->
        <template v-else-if="secDef.builtin === 'files'">
          <section-files></section-files>
        </template>
        <!-- Tabbed option list -->
        <b-tabs no-fade v-else-if="secDef.layout === 'tabs'" card>
          <b-tab
            v-for="(tabDef, tabIndex) in secDef.tabs"
            :key="tabDef.id"
            :title="tabDef.name"
            :id="`tab-${tabDef.id}`"
            :active="tabIndex === 0"
          >
            <editor-view :config="tabDef.view" :scope="tabDef.id"></editor-view>
          </b-tab>
        </b-tabs>
        <!-- Option list -->
        <editor-view
          v-else-if="secDef.layout === 'single-page'"
          :config="secDef.view"
          :scope="secDef.id"
        ></editor-view>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
import EditorView from "./EditorView.vue";

export default {
  name: "oviz-editor",
  components: { EditorView },
  props: {
    config: {
      default: { sections: [] },
    },
    editorWidth: { default: 280 },
    cardBackground: { default: "#f4f4f4" },
  },
  data() {
    return {};
  },
  computed: {
    editorStyle() {
      const w = `${this.editorWidth}px`;
      return `width:${w};min-width:${w};max-width:${w};`;
    },
  },
  created() {
    this.$nextTick(() => {
      this.$emit("create");
    });
  },
  mounted() {
    this.$nextTick(() => {
      this.$emit("mount");
    });
  },
};
</script>
