import Vue from "vue";
import Wrapper from "./Wrapper.vue";

Vue.config.productionTip = false;

export default function init(selector, initialConfig, options = {}) {
  const vm = new Vue({
    render: h => h(Wrapper, { props: { initialConfig, ...options } }),
  }).$mount(selector);

  return {
    updateConfig(config) {
      vm.$emit("updateConfig", config);
    },
    vm,
  };
}

window.OvizEditor = { init };
